<template>
<div>
    <div class="app">
        <div class="myHead pr">
            <div class="imgBox">
                <img v-if="name" src="@/static/images/my/touxiang-image@2x.png" alt="">
                <span v-if="name" style="color: #fff;">{{name}}</span>
                <!--  未登录成功时  -->
                <img v-if="!name" src="@/static/images/my/touxxiang_normal@2x.png" alt="">
                <router-link to="/login"><span v-if="!name" >登录后体验更多服务</span></router-link>
            </div>
            <div class="grade" v-if="name">
                <div class="gLeft">
                    <p class="titl"><i>正式版等级</i></p>
                    <p class="titl2"><img src="@/static/images/my/huangguan_icon@2x.png" alt=""><span>{{levelData.level_one}}级</span></p>
                </div>
                <div class="gRight">
                    <p class="titl"><i>小额版等级</i></p>
                    <p class="titl2"><img src="@/static/images/my/zuanshi_icon@2x.png" alt=""><span>{{levelData.level_two}}级</span></p>
                </div>
            </div>
        </div>
        <ul class="myList">
            <li><a href="javascript:void (0)" @click="gopush('/wallet')"><i class="zijin"></i><p>个人资金</p><span></span></a></li>
            <li><a href="javascript:void (0)" @click="flashbuy()"><i class="bi"></i><p>人民币充值提现USDT</p><span></span></a></li>
            <li><a href="javascript:void (0)" @click="gopush('/wechat')"><i class="wxcode"></i><p>上传微信二维码</p><span></span></a></li>
            <li><a href="javascript:void (0)" @click="gopush('/opinion')"><i class="feedback"></i><p>意见反馈</p><span></span></a></li>
            <li><a href="javascript:void (0)" @click="gopush('/notice')"><i class="notice"></i><p>公告</p><span></span></a></li>
            <li><a href="javascript:void (0)" @click="gopush('/invite')"><i class="yaoqing"></i><p>邀请好友</p><span></span></a></li>
            <li><a href="javascript:void (0)" @click="gopush('/backup')"><i class="backup"></i><p>备份账号</p><span></span></a></li>
            <li><a href="javascript:void (0)" @click="gopush('/setting')"><i class="set"></i><p>设置</p><span></span></a></li>
        </ul>
    </div>
    <tabbar></tabbar>
</div>

</template>


<script>
    import {wallet, userLevl} from "../../api/center";
    import {mapGetters} from 'vuex'
    import { toast, Dialog } from 'vant';
    import tabbar from '@/components/tabbar';
    export default {
        name: "Mine",
        components:{
            tabbar
        },
        computed: {
            ...mapGetters([
                'avatar',
                'name',
                'token'
            ]),
            key()
            {
                return this.$route.path
            }
        },
        data()
        {
            return {
                levelData:{}
            }
        },
        mounted()
        {
           this.getUserLevel()
        },
        methods:{
            gopush(path)
            {
                if (!this.name)
                {
                    Dialog.alert({
                        title: '系统提醒',
                        message: '请登录后访问'
                    }).then(() => {
                        this.$router.push({path:'/login'})
                    });
                } else
                {
                    this.$router.push({path:path})
                }


            },
            // 去闪币付款
            flashbuy()
            {
                if (!this.name)
                {
                    Dialog.alert({
                        title: '系统提醒',
                        message: '请登录后访问'
                    }).then(() => {
                        this.$router.push({path:'/login'})
                    });
                } else
                {
                    // 获取用户地址
                    wallet().then(res => {
                        // let url = 'https://buy.flashbuy.io/#/trade?name=usdt-erc20&cny=0&address='+res.address
                        // window.location.href = url;
                        this.$router.push({path:'/thirdRe', query:{address:res.address}})
                    })
                }
            },
            // 获取用户等级
            getUserLevel()
            {
                if (this.name)
                {
                    userLevl().then(res => {
                        this.levelData = res
                    })
                }
            }
        }
    }
</script>

<style scoped>
     @import "../../static/css/my.css";
</style>



